import React, { Component } from 'react';

import './ErrorPage.css';

class ErrorPage extends Component {

  render() {
    return (
      <div id='errorPage'>
        <h2>404</h2>
        <h4>Page not found</h4>
      </div>
    );
  }

}

export default ErrorPage;



